import ReactDOM from 'react-dom';
import { TestsAreDemoAppWrapper } from '../lib';
import App from './App';
import { Container, Divider, Label } from 'semantic-ui-react';

const Link = ({ children }: { children: string }) => <Label color="purple" as="a" href={children}>{children}</Label>

const Root = () => <>
    <Container>
        <b>Other root pages:</b>
        <Link>/?TestsAreDemo</Link>
        <Link>/indexTestingTadItself.html?TestsAreDemo</Link>
        <Link>/indexFeaturebookUiRecorded.html</Link>
        <Divider />
    </Container>
    <App />
</>

ReactDOM.render(
    <TestsAreDemoAppWrapper app={<Root />} importTestsCallback={async () => await import("./appTests")} />,
    document.getElementById('root'));